.login {
	display: flex;
	justify-content: center;
	margin-top: 20px;
}
.login .container {
	display: flex;
	flex-direction: column;
	width: 400px;
	border: 1px solid gray;
	padding: 20px 30px;
}
.login .container input {
	width: 94.5%;
	padding: 10px;
	margin-top: 5px;
	border: 1px solid #ccc;
}
.login .container hr {
	width: 100%;
	margin-top: 30px;
}
.login .container button {
	padding: 5px;
	background-color: #ff9f00;
	border: 1px solid;
	padding: 5px 10px;
	cursor: pointer;
}
